.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  color: white;
}

.color-item {
  display: flex;
  flex-wrap: wrap;
  margin: 12px;
  overflow: hidden;
}

.color-item-color {
  display: flex;
  flex-direction: row;
  padding: 24px;
}

.color-item-color > h3 {
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  font-size: 24px;
  font-weight: normal;
  padding: 0px 24px;
  margin: 0px;
}

.color-item-color > p {
  width: 100px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  font-size: 16px;
  font-weight: normal;
  padding: 0px 24px;
  margin: 0px;  
}

.color-item-color > input[type="text"] {
  height: 52px;
  width: 80px;
  font-size: 16px;
  font-weight: normal;
  border: none;
  padding: 0px 24px;
  margin: 0px;
}

.color-item-color > button {
  cursor: pointer;
  border: 0px solid;
  background-color: transparent;
}

.actions {
  display: flex;
  flex-direction: row;
}

.actions > button {
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border: 0px solid;
  font-size: 24px;
  font-weight: normal;
  padding: 0px 24px;
  margin: 12px 4px;
}
